import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
//import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class BookService {

  private booksUrl = 'api/books';  // URL to web api
  private headers = new Headers({'Content-Type': 'application/json'});

  constructor(private http: Http) { }

  // Get all the books from the API: PROMISE
  /*getAllBooks() {
    return this.http.get('/api/books')
      .map(res => res.json());
  }*/

  getAllBooks(): Promise<any[]> {
      return this.http.get(this.booksUrl)
          .toPromise()
          .then(response => response.json())
          .catch(this.handleError);
  }


  getBook(title: string) {
      return this.getAllBooks()
          .then(books => books.find(book => book.title === title));
  }


  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  update(book: any): Promise<any> {
      const url = `${this.booksUrl}/${book.title}`;
      return this.http
          .put(url, JSON.stringify(book), {headers: this.headers})
          .toPromise()
          .then(() => book)
          .catch(this.handleError);
  }

  create(title: string): Promise<any> {
      return this.http
          .post(this.booksUrl, JSON.stringify({title: title}), {headers: this.headers})
          .toPromise()
          .then(res => res.json().data)
          .catch(this.handleError);
  }

  delete(title: string): Promise<void> {
      const url = `${this.booksUrl}/${title}`;
      return this.http.delete(url, {headers: this.headers})
          .toPromise()
          .then(() => null)
          .catch(this.handleError);
  }

}



